[pokemon-card] {
  display: inline-block;
  height: 200px;
  width: 200px;
  overflow: hidden;
  background: #f2f1f1;
  border-radius: 6px;
  margin: 10px;
  margin-top: 0;
  margin-bottom: 60px;
  position: relative;
  cursor: pointer;
}
[pokemon-card] .opacity-animation {
  opacity: 1;
  transition: all linear 0.4s;
}
[pokemon-card] .transparent {
  opacity: 0;
}
[pokemon-card]:hover {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
[pokemon-card] .ajax-loader {
  background: url("ajax-loader.gif") no-repeat center;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
  cursor: progress;
}
[pokemon-card] .pokemons-header {
  font-size: 18px;
  text-align: left;
  margin-left: 10px;
  margin-top: 5px;
}
[pokemon-card] .pokemon-image {
  margin: -20px;
  text-align: center;
  height: 180px;
}
[pokemon-card] .pokemon-image img {
  width: 180px;
  height: 180px;
}
[pokemon-card] .type-buttons {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  text-transform: uppercase;
  font-size: 11px;
}
[pokemon-card] .type-buttons .left,
[pokemon-card] .type-buttons .right {
  width: 50%;
  display: inline-block;
  padding: 5px 0;
  font-weight: bold;
  text-align: center;
}
[pokemon-card] .type-buttons .left:hover,
[pokemon-card] .type-buttons .right:hover {
  opacity: 0.5;
}
[pokemon-card] .type-buttons .right {
  float: right;
  border-radius: 6px 0;
}
[pokemon-card] .type-buttons .left {
  float: left;
  border-radius: 0 6px;
}
[pokemon-card] .type-buttons .bug {
  background: #a0da5e;
}
