.pokemon-info {
  margin: auto;
  max-width: 900px;
  background: #fff;
}
.pokemon-info .opacity-animation {
  opacity: 1;
  transition: all linear 0.4s;
}
.pokemon-info .transparent {
  opacity: 0;
}
.pokemon-info rect {
  transition: all linear 1s;
}
.pokemon-info .sub-wrapper {
  padding: 0 10px;
}
.pokemon-info .header {
  text-align: center;
  padding-top: 27px;
  margin: 27px 0;
  font-size: 25px;
}
.pokemon-info .move {
  cursor: pointer;
  background: #d3f4b1;
  border-radius: 5px;
  margin: 16px 11px;
  width: 212px;
  font-size: 16px;
  padding: 9px 0;
  font-weight: normal;
}
.pokemon-info .move:hover {
  opacity: 0.8;
}
.pokemon-info .explore-more {
  background: #ffdbaf;
  border: none;
  font-weight: bold;
  border-radius: 3px;
  cursor: pointer;
  margin: 60px 0;
  font-size: 16px;
  padding: 10px 20px;
}
.pokemon-info .explore-more:hover {
  opacity: 0.8;
}
.pokemon-info h1 {
  margin-bottom: 0;
  font-size: 18px;
}
.pokemon-info .move-groups {
  text-align: center;
}
.pokemon-info .move-group {
  display: inline-block;
  vertical-align: top;
}
.pokemon-info .move-group .machine {
  background: #4a90e2;
}
.pokemon-info .move-group .tutor {
  background: #be94e6;
}
.pokemon-info .move-group .level-up {
  background: #b8e986;
}
.pokemon-info .move-group .egg-move {
  background: #ffffe0;
}
.pokemon-info .move-group .machine,
.pokemon-info .move-group .tutor {
  color: #fff;
}
.pokemon-info .abilities-block {
  width: 380px;
  height: 240px;
  border-radius: 10px;
  background: #4a90e2;
  margin-bottom: 10px;
}
.pokemon-info .control-buttons {
  height: 40px;
}
.pokemon-info .control-buttons .left,
.pokemon-info .control-buttons .right {
  min-height: 21px;
  width: 44%;
  background: #606c78;
  color: #f5f8fa;
  padding: 25px 0;
  cursor: pointer;
}
.pokemon-info .control-buttons .left:hover,
.pokemon-info .control-buttons .right:hover {
  opacity: 0.8;
}
.pokemon-info .control-buttons .left {
  float: left;
  border-radius: 0 0 15px 0;
  text-align: right;
  padding-right: 5%;
}
.pokemon-info .control-buttons .right {
  border-radius: 0 0 0 15px;
  float: right;
  text-align: left;
  padding-left: 5%;
}
.pokemon-info .control-buttons .pokemon-id {
  color: #c5cace;
}
.pokemon-info .card-wrapper {
  display: inline-block;
  width: 465px;
  vertical-align: top;
  margin-right: 20px;
}
.pokemon-info .card-wrapper .description {
  padding-left: 10px;
}
.pokemon-info .chart-block {
  background: #d8d8d8;
  border-radius: 10px;
  padding: 18px;
  width: 340px;
}
.pokemon-info [pokemon-card] {
  border-radius: 15px;
  height: 465px;
  width: 465px;
  margin-bottom: 25px;
}
.pokemon-info [pokemon-card] .type-buttons {
  font-size: 25px;
}
.pokemon-info [pokemon-card] .type-buttons > div {
  padding: 10px 0;
}
.pokemon-info [pokemon-card] .type-buttons .right {
  border-radius: 15px 0;
}
.pokemon-info [pokemon-card] .type-buttons .left {
  border-radius: 0 15px;
}
.pokemon-info [pokemon-card] .pokemons-header {
  visibility: hidden;
}
.pokemon-info [pokemon-card]:hover {
  box-shadow: none;
}
.pokemon-info [pokemon-card] .pokemon-image {
  height: 280px;
}
.pokemon-info [pokemon-card] .pokemon-image img {
  width: 430px;
  height: 430px;
}
.pokemon-info .info {
  display: inline-block;
  vertical-align: top;
  width: 120px;
  color: #fff;
  font-size: 20px;
  padding: 10px 20px;
}
.pokemon-info .info .sub-info {
  color: #000;
  font-size: 16px;
}
.pokemon-info .capitalize {
  text-transform: capitalize;
}
