.pokedex {
  text-align: center;
  margin: auto;
  width: 660px;
  padding: 0 10px;
  background: #fff;
}
.pokedex h1 {
  text-align: left;
  padding: 54px 10px;
  margin: 0;
}
.pokedex .load-more {
  background: #438bdf;
  border: none;
  font-weight: bold;
  padding: 10px 30px;
  font-size: 16px;
  border-radius: 3px;
  cursor: pointer;
  margin-bottom: 60px;
}
.pokedex .load-more:hover {
  background: #4e9cff;
}
.pokedex .float-hook {
  float: left;
}
.pokedex .clear-float-hook {
  clear: both;
}
@media only screen and (min-width: 920px) {
  .pokedex {
    padding: 0 60px;
    width: 880px;
  }
}
@media only screen and (max-width: 680px) {
  .pokedex {
    width: 440px;
  }
}
@media only screen and (max-width: 500px) {
  .pokedex {
    width: 220px;
  }
}
